"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import axios from "axios"; // axios ÎåÄÏã† fetchÎ•º ÏÇ¨Ïö©Ìï¥ÎèÑ ÎêòÏßÄÎßå, ÏùºÍ¥ÄÏÑ±ÏùÑ ÏúÑÌï¥ axiosÎ•º ÏÇ¨Ïö©Ìï©ÎãàÎã§.

// Î∞±ÏóîÎìú Îç∞Ïù¥ÌÑ∞ ÌÉÄÏûÖ Ï†ïÏùò
type PaymentStatus = 'COMPLETED' | 'PENDING' | 'FAILED'; // Prisma StatusÏôÄ ÏùºÏπòÌïòÎèÑÎ°ù Ï†ïÏùò

interface Caravan {
    name: string;
    location: string;
}

interface Reservation {
    id: string;
    caravan: Caravan;
}

interface Payment {
    id: string;
    amount: number;
    method: string;
    status: PaymentStatus;
    createdAt: string; // Í≤∞Ï†úÏùºÏãú (PrismaÏùò Í∏∞Î≥∏ ÌïÑÎìú)
    reservation: Reservation;
}

export default function PaymentHistoryPage() {
    const [payments, setPayments] = useState<Payment[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [userId, setUserId] = useState<string | null>(null);
    const router = useRouter();

    const API_URL = "http://localhost:3001/api/payments";

    useEffect(() => {
        // 1. Î°úÍ∑∏Ïù∏ Ï≤¥ÌÅ¨ Î∞è userId Ï∂îÏ∂ú
        const userJson = localStorage.getItem("user");
        
        if (!userJson) {
            router.push("/auth/login");
            return;
        }
        
        try {
            const user = JSON.parse(userJson);
            // Host IDÏôÄ ÎßàÏ∞¨Í∞ÄÏßÄÎ°ú UUID Î¨∏ÏûêÏó¥Ïù¥ÎØÄÎ°ú stringÏúºÎ°ú ÏÇ¨Ïö©
            const extractedUserId = user.id; 
            
            if (!extractedUserId) {
                router.push("/auth/login"); 
                return;
            }
            
            setUserId(extractedUserId);
            fetchPayments(extractedUserId);
            
        } catch (e) {
            router.push("/auth/login");
        }
    }, [router]);


    async function fetchPayments(currentUserId: string) {
        try {
            // üö® [ÌïµÏã¨ ÏàòÏ†ï] URL Í≤ΩÎ°ú Ïò§Î•ò Ï†úÍ±∞ Î∞è userIdÎ•º ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞Î°úÎßå Ï†ïÌôïÌïòÍ≤å Ï†ÑÎã¨
            const res = await axios.get(`${API_URL}?userId=${currentUserId}`);
            
            if (res.status !== 200) {
                throw new Error('Failed to fetch payments.');
            }
            
            setPayments(res.data);
            
        } catch (err: any) {
            console.error("Payment Fetch Error:", err);
            setError("Failed to fetch payments. Check backend API or server status.");
        } finally {
            setLoading(false);
        }
    }

    // ÏÉÅÌÉúÏóê Îî∞Î•∏ Î∞∞ÏßÄ Ïä§ÌÉÄÏùºÎßÅ
    const getStatusBadge = (status: PaymentStatus) => {
        let colorClass = "";
        switch (status) {
            case "COMPLETED":
                colorClass = "bg-green-100 text-green-800";
                break;
            case "PENDING":
                colorClass = "bg-yellow-100 text-yellow-800";
                break;
            case "FAILED":
                colorClass = "bg-red-100 text-red-800";
                break;
            default:
                colorClass = "bg-gray-100 text-gray-800";
        }
        return (
            <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${colorClass}`}>
                {status}
            </span>
        );
    };


    if (loading) return <div className="text-center py-20">Í≤∞Ï†ú Ïù¥Î†•ÏùÑ Î∂àÎü¨Ïò§Îäî Ï§ë...</div>;
    if (error) return <p className="text-center py-20 text-red-500 font-bold">{error}</p>;


    return (
        <div className="max-w-7xl mx-auto p-6">
            <h1 className="text-3xl font-bold mb-6 text-gray-800">Í≤∞Ï†ú Ïù¥Î†• Ï°∞Ìöå</h1>
            
            {payments.length === 0 ? (
                <div className="text-center py-20 bg-gray-50 rounded-lg">
                    <p className="text-gray-500 text-lg">Í≤∞Ï†ú Ïù¥Î†•Ïù¥ ÏóÜÏäµÎãàÎã§.</p>
                </div>
            ) : (
                <div className="overflow-x-auto shadow-md sm:rounded-lg">
                    <table className="min-w-full divide-y divide-gray-200">
                        <thead className="bg-gray-50">
                            <tr>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Í±∞Îûò ID</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Ïπ¥ÎùºÎ∞ò Ïù¥Î¶Ñ</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Í≤∞Ï†ú Í∏àÏï°</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Í≤∞Ï†úÏùºÏãú</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">ÏÉÅÌÉú</th>
                            </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                            {payments.map((payment) => (
                                <tr key={payment.id}>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900 truncate max-w-xs">{payment.id}</td>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                        {payment.reservation?.caravan?.name || 'N/A'}
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm font-bold text-indigo-600">
                                        ‚Ç©{payment.amount.toLocaleString()}
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                        {new Date(payment.createdAt).toLocaleDateString()}
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap">
                                        {getStatusBadge(payment.status)}
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            )}
        </div>
    );
}